


















// Import
import Vue from 'vue';
import { yandexMap, ymapMarker } from 'vue-yandex-maps';

export default Vue.extend({
  components: {
    yandexMap,
    ymapMarker,
  },
  props: {
    mapItem: {
      type: Object,
      default: null,
    },
  },
  data:() => ({
    mapSettings: {
      // apiKey: '52a7b0c7-e889-4f69-930f-1c78aacede59',
      lang: 'ru_RU',
      coordorder: 'latlong',
      version: '2.1',
    },
    mapInit: false,
  }),
  computed: {
    mapConfiguration() {
      const DEFAULT_COORDS = [-1, -1];
      const coords = this.mapItem ? this.mapItem.coords : DEFAULT_COORDS;
      const markerId = this.mapItem ? this.mapItem.id : -1;
      return {
        mapZoom: 17,
        coords,
        markerId,
      };
    },
  },
  methods: {
    mapWasInitialized() {
      this.mapInit = true;
    },
  },
});
