




























































































































// Imports
import Vue from 'vue';
import axios from 'axios';
import { Platforms } from 'typings/interface/platforms';
import { $PLATFORM_STATUS } from '@/constants/constants';
import ExpandedContent from './ExpandedContent.vue';
import EcotecMap from '@/components/EcotecMap.vue';

export default Vue.extend({
  components: {
    ExpandedContent,
    EcotecMap,
  },
  data: () => ({
    filters: {},
    expanded: [],
    selected: [],
    headers: [
      {
        text: 'Код',
        value: 'id',
        sortable: false,
      },
      {
        text: 'Адрес',
        value: 'address',
        sortable: false,
      },
      {
        text: 'Лот',
        value: 'lot',
        sortable: false,
      },
      {
        text: 'Статус',
        value: 'status',
        sortable: false,
      },
      {
        text: 'Количество контейнеров',
        value: 'containersNumbers',
        sortable: false,
      },
      {
        text: 'Объём',
        value: 'volume',
        sortable: false,
      },
      {
        text: 'Объём отгруженного с места',
        value: 'volumeShipped',
        sortable: false,
      },
    ],
    platforms: [] as Array<Platforms>,
    statusList: [
      { id: $PLATFORM_STATUS.shipped, name: 'Отгружено' },
      { id: $PLATFORM_STATUS.atRegistration, name: 'На оформлении' },
      { id: $PLATFORM_STATUS.filled, name: 'Заполнено' },
    ],
    tableScrollPosition: 0,
  }),
  computed: {
    currentItem() {
      const [currentItem] = this.expanded;
      return currentItem;
    },
  },
  mounted(): void {
    // Change url on const (default url is http://localhost:3000/platforms)
    axios
      .get('https://my-json-server.typicode.com/hastes/kuzro-build/platforms')
      .then((response) => {
        this.platforms = response.data.items;
      })
      .catch((error) => {
        // console.log(error);
      });

    this.$el.addEventListener('click', (e: any) => {
      const elementTarget = e.target;

      const twrapper = elementTarget.closest('.v-data-table__wrapper');
      const tbody = elementTarget.closest('tbody');
      const table = elementTarget.closest('table');
      const tr = elementTarget.closest('.v-data-table__expanded__row');

      const isExpanded = Boolean(this.expanded.length);

      if (tbody && tr && isExpanded) {
        const theadHeight = table.clientHeight - tbody.clientHeight;
        twrapper.scrollTo(0, tr.offsetTop - theadHeight);
      }
    });
  },
  methods: {
    getStatusColor(status: number): string {
      switch (status) {
        case $PLATFORM_STATUS.shipped:
          return 'successLight';
        case $PLATFORM_STATUS.atRegistration:
          return 'warningLight';
        case $PLATFORM_STATUS.filled:
          return 'errorLight';
        default:
          return '';
      }
    },
    getRowValue(item: never): void {
      const [expItem] = this.expanded;
      this.expanded = this.expanded.includes(item) ? [] : [item];
    },
  },
});
